import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';
import { Device } from '@ionic-native/device';
import { AttendanceService } from '../../services/attendance.service';
import { Geolocation } from '@ionic-native/geolocation';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public isPresent: boolean = false;

  constructor(
    public navCtrl: NavController,
    public device: Device,
    public geolocation: Geolocation,
    private attendanceService: AttendanceService

  ) {

  }

  onGeoLocationSuccess(geo) {
    const { latitude, longitude }: any = geo.coords;
    var postData: any = {
      latitude,
      longitude,
      isPresent: this.isPresent
    }
    postData = JSON.stringify({
      title: 'foo',
      body: 'bar',
      userId: 1
    })
    alert(`Latitude: ${latitude} ===== Lagitude: ${longitude}`);
    this.attendanceService.markAttendance("attendance", postData).subscribe(
      res => console.log(res),
      err => console.log(err)
    )
  }

  onGeoLocationError(error){
    alert(`error in getCurrentPosition(): ${error}`);
  }

  public toggleAttendance(){
    event.cancelable;
    event.preventDefault();
    this.isPresent = !this.isPresent;
    var options = {
      enableHighAccuracy: false,
      maximumAge: 60000,
      timeout: 15000,
    };
    navigator.geolocation.getCurrentPosition(this.onGeoLocationSuccess.bind(this), this.onGeoLocationError.bind(this), options);
  }

}



